___CSS_LOADER_EXPORT___.push([module.id, `.cart-container {
  padding: 2rem;
  background: #f9f9f9;
  min-height: 100vh;
}

h2 {
  font-size: 2rem;
  margin-bottom: 1.5rem;
  text-align: center;
  color: #333;
}

p {
  margin: 0.5rem 0;
  font-size: 1rem;
  color: #555;
}

/* Empty State Styling */
.empty-cart {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 50vh; /* Centered vertically */
  color: #777;
  font-size: 1.2rem;
}

.empty-cart p {
  margin-top: 1rem;
}

/* Cart Content */
.cart-content {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.cart-items {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.cart-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  background: white;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: box-shadow 0.3s ease, transform 0.3s ease;
  gap: 1rem; /* Added gap between image and details */
}

.cart-item:hover {
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.2);
  transform: translateY(-5px);
}

/* Cart Item Image */
.cart-item-image {
  width: 80px;
  height: 80px;
  overflow: hidden;
  border-radius: 4px;
  background-color: #f4f4f4;
  display: flex;
  align-items: center;
  justify-content: center;
}

.item-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.fallback-image {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  color: #ccc;
}

.item-details {
  flex: 1;
}

.item-name {
  font-size: 1.2rem;
  color: #007bff;
  margin-bottom: 0.5rem;
}

.item-price {
  font-size: 1rem;
  color: #333;
}

.remove-button {
  padding: 0.5rem 1rem;
  font-size: 1rem;
  color: white;
  background: #ff4d4d;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.remove-button:hover {
  background: #d12e2e;
}

/* Total Section */
.cart-total {
  position: sticky;
  bottom: 0;
  text-align: center;
  background: #fff;
  padding: 1rem;
  border-radius: 8px;
  box-shadow: 0 -4px 6px rgba(0, 0, 0, 0.1);
}

.cart-total h3 {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
  color: #333;
}

.total-price {
  font-size: 1.5rem;
  color: #007bff;
  font-weight: bold;
}

.buy-button {
  margin-top: 1rem;
  padding: 0.8rem 1.5rem;
  font-size: 1.2rem;
  color: white;
  background: #28a745; /* Green for buy button */
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.buy-button:hover {
  background: #218838;
}

/* Responsive Design */
@media (max-width: 768px) {
  .cart-item {
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem; /* Spacing between sections in small view */
  }

  .cart-item-image {
    width: 100%; /* Full width for smaller screens */
    height: auto;
    margin-bottom: 1rem; /* Space between image and details */
  }

  .remove-button {
    align-self: flex-end;
  }
}
